<template>
    <v-container>
        <v-card elevation="1">
            <v-card-title>
                <v-row>
                    <v-col cols="1">
                        调试器
                    </v-col>
                    <v-col cols="3">
                        <v-text-field
                            style="padding:0;"
                            v-model="siNum"
                            type="number"
                            label="SI"
                            :rules="[v => v>0 || 'num should > 0.']"
                            prepend-icon="mdi-cellphone"
                            placeholder="SI number."
                        >
                            <template v-slot:append>
                                <v-btn text @click="cmdSi(siNum)" :loading="(siCount!==-1||freshReg)">
                                    EXEC SI{{ siNum }}
                                </v-btn>
                            </template>
                        </v-text-field>
                    </v-col>
                    <v-col cols="8" style="margin:auto 0;">
                        <v-btn class="mx-1" @click="cmdC()" color="primary">Continue</v-btn>
                        <v-btn class="mx-1" @click="Pdialog=true" color="primary">Expression</v-btn>
                        <v-btn class="mx-1" @click="Xdialog=true" color="primary">Scan memory</v-btn>
                        <v-btn class="mx-1" @click="Wdialog=true" color="primary">Watch Point</v-btn>
                    </v-col>
                </v-row>
            </v-card-title>
            <v-divider class="mx-4"></v-divider>
            <v-card-text>
                <v-row style="min-height:800px;">
                    <v-col cols="6" class="left body-1 show-code">
                        <v-row style="margin:1px;"
                               v-for="(item,index) in execInst.slice(execInst.length<32?0:execInst.length-32,execInst.length)"
                               :key="index">
                            <v-col cols="2" class="line-num-col" style="border-right: 1px solid #ffffff;">
                                {{ item.split(":")[0] }}
                            </v-col>
                            <v-col cols="10" class="code-row">{{ item.split(":")[1] }}</v-col>
                        </v-row>
                    </v-col>
                    <v-col cols="6" class="left body-1">
                        <v-row class="mx-2">
                            <v-col cols="3" v-for="(item,index) in regStatus" :key="index">
                                <v-card
                                    class="mx-auto"
                                    :color="oldRegStatus[index].value===regStatus[index].value?'#26c6da':'#18808D'"
                                    dark
                                >
                                    <v-card-title style="padding:4px; width:100%;text-align:center;margin:0;">
                                        <span class="font-weight-bold" style="font-size: 14px;width:100%;">{{
                                                item.name
                                            }}</span>
                                    </v-card-title>

                                    <v-card-text class="font-weight-light"
                                                 style="font-size: 14px;padding:4px; width:100%;text-align:center;margin:0;">
                                        {{ item.value }}
                                    </v-card-text>
                                </v-card>
                            </v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
        <v-overlay :value="overlay">
            <v-row>
                <v-col cols="12" style="text-align: center;">
                    <v-progress-circular
                        indeterminate
                        size="64"
                    ></v-progress-circular>
                </v-col>
                <v-col v-for="(item,index) in loadingMsg" :key="index" cols="12" style="text-align: center;padding:0;">
                    {{ item }}
                </v-col>
            </v-row>
        </v-overlay>
        <v-dialog
            v-model="Pdialog"
            max-width="600px"
        >
            <v-card>
                <v-card-title>
                    <span class="text-h5">Expression</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                                <v-text-field
                                    style="padding:0;"
                                    v-model="pValue"
                                    label="Expression"
                                    :rules="[v => !!v || 'should have expression']"
                                    prepend-icon="mdi-cellphone"
                                    placeholder="Your Expression"
                                >
                                    <template v-slot:append>
                                        <v-btn text @click="cmdP(pValue)" :disabled="!pValue">
                                            get value
                                        </v-btn>
                                    </template>
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" class="text-body-1">
                                {{ exprBack }}
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>only support limited values</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="Pdialog = false"
                    >
                        Close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog
            v-model="Xdialog"
            max-width="600px"
        >
            <v-card>
                <v-card-title>
                    <span class="text-h5">Scan Memory</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                                <v-text-field
                                    style="padding:0;"
                                    v-model="xNum"
                                    type="number"
                                    label="bytes"
                                    :rules="[v => v>0 || 'num should > 0.']"
                                    prepend-icon="mdi-cellphone"
                                    placeholder="length"
                                >
                                </v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field
                                    style="padding:0;"
                                    v-model="xValue"
                                    label="Expression"
                                    :rules="[v => !!v || 'should have expression']"
                                    prepend-icon="mdi-cellphone"
                                    placeholder="Your Expression"
                                >
                                    <template v-slot:append>
                                        <v-btn text @click="cmdX(xNum,xValue)" :disabled="!xValue">
                                            get value
                                        </v-btn>
                                    </template>
                                </v-text-field>
                            </v-col>
                            <v-col v-for="(item,index) in xBack" :key="index" cols="12" class="text-body-1"
                                   style="padding:4px;">
                                {{ item }}
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>only support limited values</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="Xdialog = false"
                    >
                        Close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog
            v-model="Wdialog"
            max-width="600px"
        >
            <v-card>
                <v-card-title>
                    <span class="text-h5">Watch Point</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                            </v-col>
                            <v-col cols="12">
                                <v-text-field
                                    style="padding:0;"
                                    v-model="wValue"
                                    label="Expression"
                                    :rules="[v => !!v || 'should have expression']"
                                    prepend-icon="mdi-cellphone"
                                    placeholder="Your Expression"
                                >
                                    <template v-slot:append>
                                        <v-btn text @click="cmdW(wValue)" :disabled="!wValue">
                                            SET
                                        </v-btn>
                                    </template>
                                </v-text-field>
                            </v-col>
                            <v-col v-for="(item,index) in wArr" :key="index" cols="12" class="text-body-1"
                                   style="padding:4px;">
                                {{ item }}
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>only support limited values</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="Wdialog = false"
                    >
                        Close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
export default {
    name: 'MainView',
    data: () => ({
        wArr:[],
        wValue: '',
        Wdialog: false,
        xNum: '',
        xBack: [],
        Xdialog: false,
        xValue: '',
        exprBack: '',
        pValue: '',
        Pdialog: false,
        siNum: 1,
        wsc: null,
        instArr: [],
        overlay: true,
        showType: 0,
        loadingMsg: [],
        execInst: ["00000000:Welcome to Temu!"],
        regStatus: [],
        freshReg: false,
        oldRegStatus: [{"name": "$zero", "value": "0x00000000"}, {
            "name": "$at",
            "value": "0x00000000"
        }, {"name": "$v0", "value": "0x00000000"}, {"name": "v1", "value": "0x00000000"}, {
            "name": "$a0",
            "value": "0x00000000"
        }, {"name": "$a1", "value": "0x00000000"}, {"name": "$a2", "value": "0x00000000"}, {
            "name": "$a3",
            "value": "0x00000000"
        }, {"name": "$t0", "value": "0x00000000"}, {"name": "$t1", "value": "0x00000000"}, {
            "name": "$t2",
            "value": "0x00000000"
        }, {"name": "$t3", "value": "0x00000000"}, {"name": "$t4", "value": "0x00000000"}, {
            "name": "$t5",
            "value": "0x00000000"
        }, {"name": "$t6", "value": "0x00000000"}, {"name": "$t7", "value": "0x00000000"}, {
            "name": "$s0",
            "value": "0x00000000"
        }, {"name": "$s1", "value": "0x00000000"}, {"name": "$s2", "value": "0x00000000"}, {
            "name": "$s3",
            "value": "0x00000000"
        }, {"name": "$s4", "value": "0x00000000"}, {"name": "$s5", "value": "0x00000000"}, {
            "name": "$s6",
            "value": "0x00000000"
        }, {"name": "$s7", "value": "0x00000000"}, {"name": "$t8", "value": "0x00000000"}, {
            "name": "$t9",
            "value": "0x00000000"
        }, {"name": "$k0", "value": "0x00000000"}, {"name": "$k1", "value": "0x00000000"}, {
            "name": "$gp",
            "value": "0x00000000"
        }, {"name": "$sp", "value": "0x00000000"}, {"name": "$fp", "value": "0x00000000"}, {
            "name": "$ra",
            "value": "0x00000000"
        }],
        siCount: -1,
    }),
    methods: {
        handleMsg(type, msg) {
            console.log(msg)
            if (/\(temu\)n*/.test(msg)) {
                return
            }
            if (/(Unknown command)n*/.test(msg)) {
                this.$message.error('Unknown command')
                return
            }

            if(/In watchpoint/.test(msg)){
                this.$message.success(msg)
                this.siCount=-1
                return
            }

            switch (type) {
                case 0:
                    this.handleInit(msg)
                    break;
                case 1:
                    this.handleExec(msg)
                    break;
                case 2:
                    this.setInfoR(msg)
                    break;
                case 3:
                    if (/HIT GOOD TRAP/.test(msg)) {
                        this.$message.success("HIT GOOD TRAP")
                        this.$message.success("please refresh the page.")
                        this.showType = -1
                        setTimeout(() => {
                            this.$router.go(0)
                        }, 3000)
                    } else {
                        this.$message.error("HIT BAD TRAP")
                        this.$message.success("please refresh the page.")
                        this.showType = -1
                        setTimeout(() => {
                            this.$router.go(0)
                        }, 3000)
                    }
                    break;
                case 4:
                    this.exprBack = msg;
                    break;
                case 5:
                    this.xBack.push(msg)
                    break;
                case 6:
                    this.$message.success(msg)
                    this.getW()
                    break;
                case 7:
                    this.wArr.push(msg)
                    break;

            }
        },
        handleInit(msg) {
            this.loadingMsg.push(msg)
            if (msg.trim() === 'For help, type "help"') {
                setTimeout(() => {
                    this.showType = 1
                    this.overlay = false
                    this.$message.success("Init success.")
                    this.getInfoR()
                }, 1)
            }
        },
        cmdW(e) {
            this.showType = 6
            this.wsc.send(`w ${e}`)
        },
        handleExec(msg) {
            this.execInst.push(msg)
            this.siCount--
            if (this.siCount === 0) {
                this.getInfoR()
                this.siCount = -1
            }
        },
        getInfoR() {
            this.freshReg = true
            this.showType = 2
            if (this.regStatus.length !== 0) {
                this.oldRegStatus = this.regStatus
            }
            this.regStatus = []
            this.wsc.send('info r')
        },
        setInfoR(msg) {
            const [name, value] = msg.trim().split('\t')
            console.log(name, value)
            this.regStatus.push({
                name,
                value
            })
            if (name === '$ra') {
                this.freshReg = false
            }
        },
        getW() {
            this.wArr=[]
            this.showType=7
            this.wsc.send('info w')
        },
        cmdX(l, e) {
            this.xBack = []
            this.showType = 5
            this.wsc.send(`x ${l} ${e}`)
        },
        cmdC() {
            this.wsc.send('c')
            this.showType = 3
        },
        cmdSi(num) {
            this.siCount = num
            this.showType = 1
            this.wsc.send(`si ${num}`)
        },
        cmdP(e) {
            this.showType = 4
            this.wsc.send(`p ${e}`)
        },
        createWsC() {
            const port = 8848
            this.wsc = new WebSocket(`ws://localhost:${port}`);
            this.wsc.onopen = () => {
                this.loadingMsg.push("Connection open ...")
                // this.wsc.send("si 1");
            };
            this.wsc.onmessage = (evt) => {
                this.handleMsg(this.showType, evt.data)
            };
        }
    },
    created() {
        this.createWsC()
    }
}
</script>

<style scoped lang="scss">
.center {
    text-align: center;
}

.left {
    text-align: left;
}

.major-row {
    margin: 16px 0 24px 0;
    color: black;
}

.show-code {
    background-color: #282C34;
    border-radius: 4px;
}

.code-row {
    padding: 4px 4px 4px 8px;
    line-height: 100%;
    color: #ECEFF1;
    font-size: 14px;
}

.line-num-col {
    padding: 4px 8px 4px 4px;
    line-height: 100%;
    font-weight: bold;
    color: #648AA9;
    font-size: 14px;
    text-align: right !important;
}
</style>
